.tab {
  width: 50%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: rgb(235, 235, 235);
  height: 35px;
  padding: 0;
  border: 0;
}

.clicked {
  background-color: rgb(182, 182, 182);
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
}

.wrapper {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

